@media screen and(max-width:499px) {
	.content.youtube {
		figure {
			display: none;
		}
		.inner {
			width: 100%;
			padding-top: 12vmin;
			.list {
				width: 100%;
				position: relative;
				> article {
					width: 100%;
					height: 50vmin;
					text-align: center;
					background: none;
					padding: 0;
					margin-right: 0;
					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
					> P {
						position: absolute;
						bottom: 5%;
						left: 5%;
						color: #fff;
						font: 3vmin 'arial';
					}
				}
				.data {
					width: 100%;
					margin-top: 10vmin;
					margin-bottom: 20vmin;
					> article {
						display: inline-block;
						width: 33%;
						height: 35vmin;
						margin-right: 0.5%;
						margin-top: 10%;
						border-radius: 5px;
						&:nth-of-type(3n) {
							margin-right: 0;
						}
						img {
							width: 100%;
							height: 100%;
							object-fit: cover;
						}
					}
				}
			}
		}
	}
}
